<template>
  <section class="pc-container pb-4">
    <Loader v-show="loader" />
    <div class="pcoded-content">
      <!-- <div class="page-header">
        <div class="page-block">
          <div class="row align-items-center">
            <div class="col-md-6">
              <div class="page-header-title">
                <h5 class="m-b-10">Expedientes</h5>
              </div>
              <ul class="breadcrumb">
                <li class="breadcrumb-item">
                  <router-link to="/sisge">Inicio</router-link>
                </li>
                <li class="breadcrumb-item">Busqueda de expedientes</li>
              </ul>
            </div>
          </div>
        </div>
      </div> -->
      <div class="row mb-4">
        <h5 class="text-dark fw-bold">Busqueda de expedientes</h5>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-header">
              <h5 class="text-secondary">Lista de expedientes</h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table " id="miTabla">
                  <thead>
                    <tr>
                      <th>N° de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Demandante</th>
                      <th>Demandado</th>
                      <th>Pretensión</th>
                      <th class="text-center">Estado</th>
                      <th class="text-center">Interacciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-if="expedientes.length == 0" class="text-center">
                      <td colspan="7" style="font-size: 20px;">Cargando Expedientes...</td>
                    </tr>
                    <tr v-if="expedientes.length == 0 && showModal == 1" class="text-center">
                      <td colspan="7" style="font-size: 20px;">Sin datos para mostrar.</td>
                    </tr>
                    <tr v-for="expediente in expedientes" :key="expediente.exp_id">
                      <td class="col-md-2">
                        <router-link :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                          {{ expediente.numero }}
                        </router-link>
                      </td>
                      <td>{{ expediente.fecha_inicio }}</td>
                      <td class="col-md-1" v-if="expediente.procesal == 'demandante'">{{ getPersonaName(expediente) }}
                      </td>
                      <td>UNPRG</td>
                      <td v-if="expediente.procesal == 'demandado'">{{ getPersonaName(expediente) }}</td>
                      <td>{{ expediente.pretencion }}</td>
                      <td class="text-center">
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                      <td class="text-center m-0 p-0">
                        <router-link :to="{ name: 'eventos', params: { id: expediente.exp_id } }"
                          class="badge p-2 bg-links">
                          Planificar eventos
                        </router-link>
                        <router-link :to="{ name: 'archivos', params: { id: expediente.exp_id } }"
                          class="badge p-2 bg-links">
                          Registrar archivos
                        </router-link>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { badgeClass } from '@/utils/utils.js';
import '@/assets/css/fileStatus.css';
import Loader from '../../components/Loader/LoaderModal.vue';
export default {
  name: 'Buscar',
  components: {
    Loader
  },
  created() {
    //inicializar en 0  todo;
    this.expedientes = [];
    this.dataTable = null;
    this.exp = {};
    this.showModal = false;
    this.idExpediente = null;
    // this.cargarexp();

  },

  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
    this.cargarexp();
  },

  data() {
    return {
      expedientes: [],
      dataTable: null,
      exp: {},
      showModal: false,
      idExpediente: null,
      loader: false
    };
  },

  methods: {
    cargarexp() {
      this.loader = true;
      axios.get(`${this.$store.getters.get__url}/proceeding`, {
        params: {
        },
        headers: {
          Authorization: `Bearer ${this.$store.getters.get__token}`,
        },
      }).then((response) => {
        

        this.expedientes = response.data.data;
        this.initDataTable();
      })
        .catch((error) => {
          this.loader = false;
          console.error(error);
        });
    },
    initDataTable() {
      if (!this.dataTable || !$.fn.DataTable.isDataTable('#miTabla')) {
        if (this.dataTable) {
          this.dataTable.destroy();
        }
        this.$nextTick(() => {
          this.dataTable = $('#miTabla').DataTable({
            language: {
              url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
            },
          });
          feather.replace();
        });
      } else {
        this.dataTable.clear().rows.add(this.expedientes).draw(); // Actualizar datos si la tabla ya está inicializada
      }
    },
    badgeClass,
    getPersonaName(expediente) {
      if (expediente.tipo_persona === 'natural') {
        return `${expediente.apellido_paterno} ${expediente.apellido_materno} ${expediente.nombres}`;
      } else {
        return expediente.razon_social;
      }
    },

  },

  beforeDestroy() {
    if (this.dataTable) {
      this.dataTable.destroy();
    }
  },

};
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}



.container-div {
  position: relative;
}

.content-events {
  position: absolute;
  bottom: 5em;
  right: 5em;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #ccc;
  z-index: 1;
}

.bg-links {
  background: #e3e1fc;
  color: #7267EF;
  border: 1px solid #e3e1fc;
  margin: 2.5px !important;
}
</style>
